* {
	padding: 0;
	margin: 0;
	border: 0;
}

*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
	font-family: inherit;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: inherit;
}

body {
	background-color: rgb(0, 0, 0);
}

.app {
	padding: 30px 0;
}

.controlBtn {
	margin: 5px 5px;
	padding: 5px 15px;
	min-width: 150px;
	background-color: #3b5998;
	color: white;
	border-radius: 8px;
	font-size: 18px;
	cursor: pointer;
	border: 1px solid hsla(0,0%,100%,.7);
	box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
	transition: 0.3s all;
	&:hover {
		background-color: #3b589888;
	}
}

.room {
  padding: 20px;
	color: white;
	& label {
		font-size: 30px;
	}
	& input {
		margin: 0 20px 10px 0;
		padding: 10px 15px;
		min-width: 300px;
		border-radius: 8px;
		font-size: 18px;
		border: 2px solid hsla(0,0%,100%,.7);
		box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
		&:focus {
			border: 2px solid #0051ff;
			box-shadow: 0 0 8px 0 #0051ff;
		}
	}
	&__name {
		border: 2px solid #3b5998;
	}
}

