			border: 2px solid hsla(0,0%,100%,.7);
			box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
			&:focus {
				border: 2px solid #0051ff;
				box-shadow: 0 0 8px 0 #0051ff;
			}
		}
		&__name {
			border: 2px solid #3b5998;
		}
	}
