.chat-app {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: sans-serif;
  width: 90%;
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid hsla(0,0%,100%,.7);
  box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
  &__controlBtns {
    align-self: flex-end;
    display: flex;
    margin: 20px 5px 30px 0;
  }
}

.header {
  padding: 10px 0;
  border-bottom: 1px solid hsla(0,0%,100%,.7);
  box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
  width: 100%;
  text-align: center;
  font-size: 30px;
  color: white;
}

.messages {
  display: grid;
  grid-template: auto / 1fr;
  row-gap: 15px;
  width: 100%;
  overflow-y: auto;
  padding: 20px 100px;
  margin-bottom: 10px;
  &__currentUser {
    justify-self: self-end;
    width: 50%;
    background-color: rgb(4, 133, 197);
  }
  &__otherUser {
    width: 50%;
    background-color: rgb(10, 161, 129);
  }
  &__item {
    display: grid;
    grid-template: repeat(2, auto) / 1fr 105px;
    align-items: start;
    column-gap: 15px;
    border-radius: 22px;
    min-height: 80px;
    overflow: hidden;
    padding: 16px;
    border: 1px solid hsla(0,0%,100%,.7);
    box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
  }
  &__user {
    grid-column: 1/3;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin-bottom: 3px;
  }
  &__text {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    overflow-wrap: break-word;
  }
  &__time {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}

.new-message-form {
  display: flex;
  width: 100%;
  padding: 10px;
}

.new-message-input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  font-size: 16px;
  color: #8b8b8b;
  padding: 10px;
  border-radius: 5px 0 0 5px;
}

.send-button {
  padding: 5px 15px;
  border: 1px solid hsla(0,0%,100%,.7);
  box-shadow: 0 0 8px 0 hsla(0,0%,100%,.3);
  border-radius: 8px;
  background: #3b5998;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  transition: 0.3s all;
    &:hover {
      background-color: #3b589888;
    }
}

@media (max-width: 1200px) {
  .messages {
    padding: 20px;
  }
}

@media (max-width: 991px) {
  .messages {
    padding: 20px 40px;
    &__currentUser {
      justify-self: self-end;
      width: auto;
    }
    &__otherUser {
      justify-self: start;
      width: auto;
    }
  }
}

@media (max-width: 767px) {
  
}

@media (max-width: 575px) {
  .messages {
    padding: 20px 10px;
    &__currentUser {
      justify-self: self-end;
      width: 100%;
    }
    &__otherUser {
      justify-self: start;
      width: 100%;
    }
    &__item {
      grid-template: 1fr / 1fr;
      row-gap: 5px;
    }
    &__user {
      grid-column: 1/2;
    }
  }

  .new-message-form {
    flex-direction: column;
  }

  .send-button {
    margin: 0 auto;
    margin-top: 20px;
    width: 200px;
  }

  .chat-app {
    &__controlBtns {
      align-self: center;
      display: flex;
      flex-direction: column;
    }
  }

  .header {
    font-size: 22px;
  }
}